import React from 'react';
import TextInput from './parts/TextInput';

const SearchForm = () => (
  <form id="searchForm">
      <h3>Search for a user</h3>
        <TextInput 
            inputLabel="Search by Name"
            inputVal="Search"
            inputRef="nameSearch"
            inputPlaceholder="Search by name..."
        />
        <input
            type="submit"
            value="Search"
        />

  </form>
);

export default SearchForm;